/* I take no responsability for this auto-generated styling */


/* Reset some basic elements to remove default margin/padding */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Set a global font family */
  body {
    font-family: 'Arial', sans-serif; /* Customize with your preferred font */
    background-color: #ddf5e0; /* Set a global background color */
    color: #383e37; /* Default text color */
    line-height: 1.6; /* Improve readability */
  }
  
  /* Make sure the main content area is centered and has some padding */
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }

  .form-group {
    margin-bottom: 15px;
  }

  .form-input {
    margin: 15px;
  }

  .form-checkbox {
    margin: 15px;
  }
  
  /* Add some basic styling for headings */
  h1, h2, h3, h4, h5, h6 {
    color: #222;
    margin-bottom: 10px;
  }
  
  /* Links default styling */
  a {
    color: #007bff;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  /* Button styling */
  button {
    background-color: #fc7f6c;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #fbaa9e;
  }
  
  /* Add a global padding or margin to sections if needed */
  section {
    padding: 40px 0;
  }


  /* COLORS */

  :root {
    --primary-color: #0d4f21;
    --primary-color-hover: #446a50;
    --secondary-color: #fc7f6c;
    --secondary-color-hover: #fbaa9e;
    --background-color-light: #ddf5e0;
    --background-color-dark: #839781;
    --text-color-light: #ddf5e0;
    --text-color-dark: #383e37;
    --link-color: #1a7b44;
    --link-hover-color: #166837;
  }